import term from "./term.json";
import price from "./price.json";
import {getArea, getTerm} from "./../../../selectors";

const Ratios = {
	term,
	price
};

const getListByProp = (type, prop) => (
	Ratios[type][prop] || []
);

const getRatioValueData = (type, prop, value) => (
	getListByProp(type, prop).find(value_data => (
		value_data.name === value && value_data.type === 'ratio'
	)) || {}
);

const getFixedValueData = (type, prop, value) => (
	getListByProp(type, prop).find(value_data => (
		value_data.name === value && value_data.type === 'fixed'
	)) || {}
);

const getFixedValueByAreaRange = (ranges = [], area) => (
	ranges.find(range => (
		area >= range.min && (area <= range.max || !range.max)
	)) || {}
);

const getRatio = (type, prop, value) => {
	if (value instanceof Array) {
		return value.reduce((a, b) => (
			a * getRatio(type, prop, b)
		), 1);
	} else {
		return (
			getRatioValueData(type, prop, value).value || 1
		)
	}
};

const getFixed = (type, prop, value, params) => {
	if (value instanceof Array) {
		return value.reduce((a, b) => (
			a + getFixedProxy(type, prop, b, params)
		), 0);
	} else {
		const value_data = getFixedValueData(type, prop, value);

		if (value_data.dependency === 'area' && value_data.value) {
			return (
				!value_data.min || (value_data.value * params.area) >= value_data.min ?
					value_data.value * params.area :
					value_data.min
			)
		}

		if (value_data.dependency === 'area' && value_data.ranges) {
			return getFixedValueByAreaRange(
				value_data.ranges,
				params.area
			).value || 0
		}

		if (value_data.dependency === 'term' && value_data.value) {
			return value_data.value * params.term + (
				value_data.min || 0
			)
		}

		if (value_data.dependency === 'area_and_term' && value_data.ranges) {
			return (getFixedValueByAreaRange(
				value_data.ranges,
				params.area
			).value || 0) * params.term
		}

		if (value_data.type === 'fixed' && value_data.ranges) {
			return (getFixedValueByAreaRange(
				value_data.ranges,
				params.area
			).value || 0)
		}

		if (value_data.type === 'fixed' && value_data.value) {
			return value_data.value;
		}

		return 0
	}
};

const getFixedProxy = (type, prop, value, params) => {
	const result = getFixed(type, prop, value, params);

	if (result > 0) {
		console.log(`prop: "${prop}"; value: "${value}"; price: "${result}"`);
	}

	return result
};

const getDataProps = (data) => (
	Object.keys(data)
);

export default (state) => {
	const data = state.data;

	const params = {
		area: getArea(state),
		term: getTerm(state)
	};

	const price_result = getDataProps(data).reduce((a, b) => (
		a * getRatio('price', b, data[b])
	), 1);

	const term_result = getDataProps(data).reduce((a, b) => (
		a * getRatio('term', b, data[b])
	), 1);

	const additional_price = getDataProps(data).reduce((a, b) => (
		a + getFixedProxy('price', b, data[b], params)
	), 0);

	return {
		price: price_result * additional_price,
		term: term_result
	}
}


