const PRICE_PROPS = ['services'];
const AREA_PROP = 'building_area';
const TERM_PROP = '';

export const getPrice = (state) => (
	PRICE_PROPS.reduce((value, prop) => (
		value + state.data[prop]
	), 0)
);

export const getArea = (state) => (
	Number(state.data[AREA_PROP]) || 0
);

export const getTerm = (state) => (
	Number(state.term || state.data[TERM_PROP]) || 0
);

export const formatPrice = (value) => (
	'' + (
		new Intl.NumberFormat('us-US', {maximumFractionDigits: 0}).format(value)
	).replace(',', ' ')
);

export const formatNumber = (value) => (
	!isNaN(Number(value)) ?
		String(value).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1 ') :
		value
);



