export default class LanguageSelect extends Component {

	state = {
		isClearable: false,
		isSearchable: false
	};

	handleChange = (languages, activeLanguage, setActiveLanguage) => {
		// setActiveLanguage(languages.code)
		// console.log(languages);
		console.log('change');
	};


	render() {

		const {
			isClearable,
			isSearchable
		} = this.state;

		return (
			<Select
				onChange={this.handleChange}
				options={options}
				defaultValue={options[0]}
				isClearable={isClearable}
				isSearchable={isSearchable}
			/>
		)
	}
}




