import React, { Component } from 'react';
import { renderToStaticMarkup } from "react-dom/server";
import './styles/styles.css';

import {
	Route,
	BrowserRouter,
	Switch
} from "react-router-dom";

import {
	LocalizeProvider,
	withLocalize
} from "react-localize-redux";

import TopBar from "./components/top_menu/top_bar";
import Main from "./views/main";
import Content from "./views/content";

class App extends Component {
	constructor(props) {
		super(props);
		this.submitContactData = this.submitContactData.bind(this);
	}

	state = {
		is_contact_data_exist: false
	};

	submitContactData() {
		this.setState({
			is_contact_data_exist: true
		});
	}

	render() {
		const {is_contact_data_exist} = this.state;

		return (
			<LocalizeProvider>
				<BrowserRouter>
					<div className="App">
						<TopBar />
						<Switch>
							<Route exact path="/" component={Main} language={this.props.languages} />
							<Route render={() => (
								<Content is_contact_data_exist={is_contact_data_exist}
										 submitContactData={this.submitContactData}
								/>
							)} />
						</Switch>
					</div>
				</BrowserRouter>
			</LocalizeProvider>
		);
	}
}

export default withLocalize(App);



