import React, { Component } from 'react';
import { connect } from 'react-redux';
import { NavLink } from "react-router-dom";
import * as Actions from "../actions";
import {formatPrice, formatNumber} from '../selectors';

const POPUP_ACTIVE_CLASS = '_blurred';

const Checkbox = ({data, option}) => (
	(data[option.name] || []).map((value, index) => {
		const match = option.options.find(item => (item.value === value));

		if (match) {
			return (
				<div className="estimate-description" key={`${value}_${index}`}>
					- {match.title}
				</div>
			)
		}

		for (let item of option.options) {
			if (item.options) {
				for (let item_option of item.options) {
					if (item_option.value === value) {
						return (
							<div className="estimate-description" key={`${value}_${index}`}>
								- {`${item.title} / ${item_option.title}`}
							</div>
						)
					}
				}
			}
		}
	})
);

class EstimatedCost extends Component {
	constructor(props) {
		super(props);
		this.closePopup = this.closePopup.bind(this);
	}

	componentDidUpdate(prevProps) {
		if (!prevProps.is_contact_data_exist && this.props.is_contact_data_exist) {
			this.wrapper.classList.remove(POPUP_ACTIVE_CLASS)
		}
	}

	shouldComponentUpdate(nextProps) {
		return false;
	}

	closePopup() {
		this.wrapper.classList.remove(POPUP_ACTIVE_CLASS);
		setTimeout(this.props.submitContactData, 2000);
	}

	render() {
		const {steps, data, price, setStep} = this.props;

		return(
			<div className={`estimate-page ${this.props.is_contact_data_exist ? '' : POPUP_ACTIVE_CLASS}`}
				 ref={el => this.wrapper = el}
			>
				<div className="estimate-head">
					<div className="estimate-head-title">
						Total Price - {formatPrice(price)}
					</div>
					<div className="estimate-head-subtitle">
						The cost is approximate, the final cost may be different.
					</div>
					<div className="estimate-head-links">
						<a href="javascript:void(0);" className="estimate-head-link">Save</a>
						<span className="estimate-head-separator" />
						<a href="javascript:void(0);" className="estimate-head-link">Download</a>
						<span className="estimate-head-separator" />
						<a href="javascript:void(0);" className="estimate-head-link">Print</a>
						<span className="estimate-head-separator" />
						<a href="javascript:void(0);" className="estimate-head-link">Share</a>
					</div>
				</div>

				<div className="estimate-steps">
					{steps.filter(step => step.options.length).map(step => (
						<div className="estimate-step" key={`estimated_cost_table_step_${step.index}`}>
							<div className="estimate-step-head">
								<div className="estimate-step-title">
									{step.path_title}
								</div>

								<NavLink replace to={`/${step.path}`} onClick={() => setStep(step.id)}>
									Edit
								</NavLink>
							</div>

							{step.options.filter(option => (
								typeof option.checked === 'undefined' || option.checked
							)).map(option => (
								<div className="estimate-description" key={`estimated_cost_table_option_${step.index}_${option.id}`}>
									{option.type === 'select' ? (
										<div className="estimate-description-details">
											<div>- {option.title}</div>
											<div>{(option.options.find(item => item.value === data[option.name]) || {}).label}</div>
										</div>
									) : option.type === 'radio' || option.type === 'approach_radio' ? ( //done
										<div className="estimate-description-title">
											- {(option.options.find(item => item.value === data[option.name]) || {}).title}
										</div>
									) : option.type === 'checkbox' ? (
										<Checkbox data={data} option={option} />
									) : (
										<div className="estimate-description-details">
											<div>- {option.title}</div>
											<div>{formatNumber(data[option.name])}</div>
										</div>
									)}
								</div>
							))}
						</div>
					))}
					{/*<p className="will-contact">Let's discuss your final price!</p>*/}
				</div>

				<div className="estimate-popup">
					<div className="estimate-popup-title">
						Contact Information
					</div>
					<div className="estimate-popup-subtitle">
						Please complete your contact information to proceed.
					</div>
					<div className="estimate-popup-form">
						<label htmlFor="">Your Name</label>
						<input type="text"/>
					</div>
					<div className="estimate-popup-form">
						<label htmlFor="">Company Name</label>
						<input type="text"/>
					</div>
					<div className="estimate-popup-form">
						<label htmlFor="">Phone Number</label>
						<input type="text"/>
					</div>
					<div className="estimate-popup-form">
						<label htmlFor="">Email Address</label>
						<input type="text"/>
					</div>
					<div className="estimate-popup-form">
						<button className="btn btn-next" onClick={this.closePopup}>
							Show Estimated Cost
						</button>
					</div>
					<div className="estimate-popup-notice">
						Your information will not be shared to outside entities.
					</div>
				</div>
			</div>
		)
	}
}

const mapStateToProps = (state) => ({
	...state
});

const mapDispatchToProps = (dispatch) => ({});

export default connect(
	mapStateToProps,
	mapDispatchToProps
)(EstimatedCost);


