import getRatios from './components/configurator/ratios';

export const SET_STEP = 'SET_STEP';

export const setStep = step => ({
	type: SET_STEP,
	step
});

export const SET_PROP = 'SET_PROP';

export const setProp = (prop, value) => ({
	type: SET_PROP,
	prop,
	value
});

export const onChange = (prop, value) => (dispatch) => {
	dispatch(
		setProp(prop, value)
	);

	dispatch(
		setPrice()
	);
};

export const SET_PRICE = 'SET_PRICE';

export const setPrice = () => (dispatch, getState) => {
	console.log('data', getState().data);

	const ratios = getRatios(getState());

	console.log('price', ratios.price);
	console.log('term', getState().term * ratios.term);

	dispatch({
		type: SET_PRICE,
		price: ratios.price
	})
};


